import { create } from 'zustand'

export interface Hotel {
  id: string
  name: string
  location: string
  rating: number
  reviews: number
  images: string[]
  amenities: string[]
  prices: {
    booking: number
    agoda: number
    trip: number
    google: number
    sgCheep: number
  }
  originalPrice: number
}

export interface SearchParams {
  destination: string
  checkIn: string
  checkOut: string
  guests: string
  rooms: number
}

interface AppState {
  // Search state
  searchParams: SearchParams
  setSearchParams: (params: Partial<SearchParams>) => void
  
  // Results state
  hotels: Hotel[]
  setHotels: (hotels: Hotel[]) => void
  loading: boolean
  setLoading: (loading: boolean) => void
  
  // Comparison state
  selectedHotels: string[]
  addToComparison: (hotelId: string) => void
  removeFromComparison: (hotelId: string) => void
  clearComparison: () => void
}

export const useAppStore = create<AppState>((set, get) => ({
  // Search state
  searchParams: {
    destination: '',
    checkIn: '',
    checkOut: '',
    guests: '2 adults',
    rooms: 1,
  },
  setSearchParams: (params) =>
    set((state) => ({
      searchParams: { ...state.searchParams, ...params },
    })),

  // Results state
  hotels: [],
  setHotels: (hotels) => set({ hotels }),
  loading: false,
  setLoading: (loading) => set({ loading }),

  // Comparison state
  selectedHotels: [],
  addToComparison: (hotelId) =>
    set((state) => ({
      selectedHotels: state.selectedHotels.includes(hotelId)
        ? state.selectedHotels
        : [...state.selectedHotels, hotelId],
    })),
  removeFromComparison: (hotelId) =>
    set((state) => ({
      selectedHotels: state.selectedHotels.filter((id) => id !== hotelId),
    })),
  clearComparison: () => set({ selectedHotels: [] }),
}))
