'use client'

import { Calendar, MapPin, Users, Search } from 'lucide-react'
import { useAppStore } from '../store/useAppStore'
import { bookingService } from '../services/BookingService'

export default function SearchForm() {
  const { searchParams, setSearchParams, setHotels, setLoading } = useAppStore()

  const handleInputChange = (field: string, value: string) => {
    setSearchParams({ [field]: value })
  }

  const handleSearch = async () => {
    if (!searchParams.destination || !searchParams.checkIn || !searchParams.checkOut) {
      alert('Please fill in all required fields')
      return
    }

    setLoading(true)
    try {
      const hotels = await bookingService.searchHotels(searchParams)
      setHotels(hotels)
      console.log('Search results:', hotels)
    } catch (error) {
      console.error('Search failed:', error)
      alert('Search failed. Please try again.')
    } finally {
      setLoading(false)
    }
  }

  return (
    <div className="card">
      <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
        {/* Destination */}
        <div className="relative">
          <label className="block text-sm font-medium text-gray-700 mb-2">
            Where to?
          </label>
          <div className="relative">
            <MapPin className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
            <input
              type="text"
              value={searchParams.destination}
              onChange={(e) => handleInputChange('destination', e.target.value)}
              placeholder="Singapore, Marina Bay..."
              className="w-full pl-10 pr-3 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-cheep-primary focus:border-transparent"
            />
          </div>
        </div>

        {/* Check-in */}
        <div className="relative">
          <label className="block text-sm font-medium text-gray-700 mb-2">
            Check-in
          </label>
          <div className="relative">
            <Calendar className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
            <input
              type="date"
              value={searchParams.checkIn}
              onChange={(e) => handleInputChange('checkIn', e.target.value)}
              className="w-full pl-10 pr-3 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-cheep-primary focus:border-transparent"
            />
          </div>
        </div>

        {/* Check-out */}
        <div className="relative">
          <label className="block text-sm font-medium text-gray-700 mb-2">
            Check-out
          </label>
          <div className="relative">
            <Calendar className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
            <input
              type="date"
              value={searchParams.checkOut}
              onChange={(e) => handleInputChange('checkOut', e.target.value)}
              className="w-full pl-10 pr-3 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-cheep-primary focus:border-transparent"
            />
          </div>
        </div>

        {/* Guests */}
        <div className="relative">
          <label className="block text-sm font-medium text-gray-700 mb-2">
            Guests
          </label>
          <div className="relative">
            <Users className="absolute left-3 top-3 h-4 w-4 text-gray-400" />
            <select
              value={searchParams.guests}
              onChange={(e) => handleInputChange('guests', e.target.value)}
              className="w-full pl-10 pr-3 py-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-cheep-primary focus:border-transparent appearance-none"
            >
              <option>1 adult</option>
              <option>2 adults</option>
              <option>2 adults, 1 child</option>
              <option>2 adults, 2 children</option>
              <option>3 adults</option>
              <option>4 adults</option>
            </select>
          </div>
        </div>
      </div>

      {/* Search Button */}
      <div className="mt-6 text-center">
        <button
          onClick={handleSearch}
          className="btn-primary inline-flex items-center space-x-2 px-8 py-3 text-lg"
        >
          <Search className="h-5 w-5" />
          <span>Compare Prices</span>
        </button>
      </div>
    </div>
  )
}
