  // Get our best aggregated prices
  async getSgCheepPrices(hotelIds: string[]): Promise<Record<string, number>> {
    try {
      // Get prices from all platforms and find the best deal
      const [booking, agoda, trip, google] = await Promise.all([
        this.getBookingPrices(hotelIds),
        this.getAgodaPrices(hotelIds),
        this.getTripPrices(hotelIds),
        this.getGooglePrices(hotelIds)
      ])

      return hotelIds.reduce((acc, id) => {
        const prices = [
          booking[id] || Infinity,
          agoda[id] || Infinity,
          trip[id] || Infinity,
          google[id] || Infinity
        ]
        const bestPrice = Math.min(...prices)
        // Our price is 5-15% better than the best market price
        const discount = Math.random() * 0.1 + 0.05 // 5-15%
        acc[id] = Math.floor(bestPrice * (1 - discount))
        return acc
      }, {} as Record<string, number>)
    } catch (error) {
      console.error('Error calculating SG Cheep prices:', error)
      return {}
    }
  }

  // Get all prices for comparison
  async getAllPrices(hotelIds: string[]): Promise<Record<string, Record<string, number>>> {
    try {
      const [booking, agoda, trip, google, sgCheep] = await Promise.all([
        this.getBookingPrices(hotelIds),
        this.getAgodaPrices(hotelIds),
        this.getTripPrices(hotelIds),
        this.getGooglePrices(hotelIds),
        this.getSgCheepPrices(hotelIds)
      ])

      return {
        booking,
        agoda,
        trip,
        google,
        sgCheep
      }
    } catch (error) {
      console.error('Error fetching all prices:', error)
      throw new Error('Failed to fetch prices')
    }
  }

